.ReactModal__Overlay {
  opacity: 0;
  transform: scale(0.95);
  transition: all 200ms ease-in-out;
}

.ReactModal__Overlay--after-open {
  opacity: 1;
  transform: scale(1);
}

.ReactModal__Overlay--before-close {
  opacity: 0;
  transform: scale(0.95);
}

.dialog {
  position: absolute;
  border: 1px solid rgb(204, 204, 204);
  background: rgb(255, 255, 255);
  overflow: auto;
  border-radius: 4px;
  outline: none;
  padding: 2px;
  width: 80%;
  max-width: 400px;
}

.dialog-content {
  padding: 18px;
  min-height: 70px;
}

.dialog-icon {
  position: absolute;
}

.dialog-text {
  font-size: 1.5em;
  text-align: center;
  line-height: 70px;
  color: #696969;
}

.dialog-interaction {
  height: 100%;
  overflow: hidden;
  background: #444;
}

.dialog-button {
  text-align: center;
  margin-top: 0;
  transition: all 0.5s;
}

.dialog-button > button {
  display: block;
  width: 100%;
  height: 50px;
  color: #fff;
  background: #444;
  border: 0;
  outline: none;
  cursor: pointer;
  transition: all 0.5s;
}

.dialog-button > button:hover {
  color: #444;
  background: #6cdfea;
}

.dialog-button .dialog-button-yes,
.dialog-button .dialog-button-no {
  display: inline-block;
  width: 50%;
}

.dialog-button .dialog-button-yes {
  color: #a5c75f;
}

.dialog-button .dialog-button-no {
  color: #f56558;
}

.dialog-button .dialog-button-yes:hover {
  color: #fff;
  background: #a5c75f;
}

.dialog-button .dialog-button-no:hover {
  color: #fff;
  background: #f56558;
}

@keyframes spin {
  from {
    transform: rotate(0);
  }
  to {
    transform: rotate(359deg);
  }
}

.spinner-box {
  width: 70px;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
}

.circle-border {
  width: 35px;
  height: 35px;
  padding: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 50%;
  background: rgb(63, 249, 220);
  background: linear-gradient(
    0deg,
    rgba(63, 249, 220, 0.1) 33%,
    rgba(63, 249, 220, 1) 100%
  );
  animation: spin 0.8s linear 0s infinite;
}

.circle-core {
  width: 100%;
  height: 100%;
  background-color: #fff;
  border-radius: 50%;
}

.sa-success {
  border-radius: 50%;
  border: 4px solid #3ff9dc;
  width: 40px;
  height: 40px;
  margin: 11px;
  padding: 0;
  position: relative;
  background-color: #fff;
}

.sa-success:after,
.sa-success:before {
  background: #fff;
  content: '';
  width: 31px;
  height: 60px;
  position: absolute;
  transform: rotate(45deg);
}

.sa-success:before {
  border-radius: 40px 0 0 40px;
  width: 17px;
  height: 47px;
  top: -9px;
  left: 3px;
  transform-origin: 27px 38px;
  transform: rotate(-45deg);
}

.sa-success:after {
  border-radius: 0 60px 60px 0;
  left: 15px;
  top: -6px;
  transform-origin: 0 30px;
  transform: rotate(-45deg);
  animation: rotatePlaceholder 4.25s ease-in;
}

.sa-success-placeholder {
  border-radius: 50%;
  border: 4px solid rgba(63, 249, 220, 0.25);
  width: 40px;
  height: 40px;
  position: absolute;
  top: -4px;
  left: -4px;
  z-index: 2;
}

.sa-success-fix {
  background-color: #fff;
  width: 3px;
  height: 50px;
  position: absolute;
  top: 0;
  left: 13px;
  transform: rotate(-45deg);
  z-index: 1;
}

.sa-success-tip,
.sa-success-long {
  background-color: #3ff9dc;
  border-radius: 2px;
  height: 3px;
  position: absolute;
  z-index: 2;
}

.sa-success-tip {
  left: 7px;
  top: 23px;
  transform: rotate(45deg);
  width: 13px;
  animation: animateSuccessTip .75s;
}

.sa-success-long {
  right: 4px;
  top: 19px;
  transform: rotate(-45deg);
  width: 24px;
  animation: animateSuccessLong .75s;
}

@keyframes animateSuccessTip {
  0%, 54% {
    width: 0;
    left: 1px;
    top: 10px;
  }
  70% {
    width: 25px;
    left: -4px;
    top: 19px;
  }
  84% {
    width: 9px;
    left: 11px;
    top: 24px;
  }
  100% {
    width: 13px;
    left: 7px;
    top: 23px;
  }
}

@keyframes animateSuccessLong {
  0%, 65% {
    width: 0;
    right: 23px;
    top: 27px;
  }
  84% {
    width: 28px;
    right: 0;
    top: 18px;
  }
  100% {
    width: 24px;
    right: 4px;
    top: 19px;
  }
}

@keyframes rotatePlaceholder {
  0%, 5% {
    transform: rotate(-45deg);
  }
  100%, 12% {
    transform: rotate(-405deg);
  }
}

.sa-warning {
  border-radius: 50%;
  border: 4px solid #F8D486;
  width: 40px;
  height: 40px;
  margin: 11px;
  position: relative;
  background-color: #fff;
}

.sa-warning-body,
.sa-warning-dot {
  background-color: #F8BB86;
  width: 3px;
  margin-left: -1px;
  position: absolute;
  left: 50%;
  z-index: 2;
}

.sa-warning-body {
  border-radius: 2px;
  height: 24px;
  top: 5px;
}

.sa-warning-dot {
  border-radius: 50%;
  height: 3px;
  bottom: 5px;
}
